.callout.banner-section{
  background-color: $deep-yellow-color;
  padding: 100px 20px 75px 20px;
  margin: 0;
}

.banner-inner{
  max-width: 600px;
  margin: 0 auto;
}

.banner-text{
  font-size: 32px;
  line-height: 40px;
  color: $grey-yellow-color;
  margin-bottom: 35px;

  .amount{
    font-size: 43px;
    line-height: 49px;
    color: #897400;
  }
}

/* ------- style for search form --------- */

.search-form{

}

input[type=text],
input[type=password] {
  box-sizing : border-box;
}

input {
  outline: none;
}
input[type=search] {
  -webkit-appearance: textfield;
  -webkit-box-sizing: content-box;
  font-family: inherit;
  font-size: 100%;
}
input::-webkit-search-decoration,
input::-webkit-search-cancel-button {
  display: none;
}

input[type=search],input[type=search]:focus,input[type=search]:active,input[type=search]:hover {
  background: rgba(255,255,255,0.3);
  border: none;
  padding: 32px 37px;
  width: 100%;
  box-sizing : border-box;
  border-radius: 10em;
  transition: all .5s;
  box-shadow: 0px 2px 20px 0px rgba(0,0,0,0.50);
  border-radius: 51px;
}

input:-moz-placeholder {
  color: #756200;
}
input::-webkit-input-placeholder {
  color: #756200;
}

/*style for chart container*/

.each-chart-section{
  min-height: 300px;
  padding: 45px 45px 20px;
  background: #f7f7f7;
  //border: solid 1px blue;
  &:nth-child(1){
    background: #F1F1F1;
  }

  &:nth-child(4){
    background: #F1F1F1;
  }

  &:nth-child(5){
    background: #F1F1F1;
  }

  &:nth-child(7){
    background: #F1F1F1;
  }

  .section-header{
    h3{
      width: calc(100% - 145px);
      float: left;
      @include  small-title();
      margin-bottom: 25px;
    }

    .right-section{
      width: 145px;
      select{
        margin-bottom: 0;
        border-radius: 15px;
        border: solid 1px #484848;
        padding: 4px 12px;
        font-size: 14px;
        line-height: 20px;
        color: #484848;
        color: $light-grey-color;
        height: auto;
        font-weight: normal;
        font-family: ubunto-regular;
      }
    }
  }

  .chart-wrap{
    min-height: 150px;
  }
}

.section-header{
  @include small-title();
}

/* -------- style for chart section ----------- */

.chart-section-wrap{
  margin-bottom: 50px;

  ul.breadcrumbs{
      margin-bottom: 0;
  }

  .breadcrumbs{
    li:not(:last-child)::after{
      content: none;
    }

    li{
      @include small-title();
      text-transform: capitalize;
      margin-bottom: 25px;
    }
  }
}


.indicator{
  color: $dark-grey-color;
  position: relative;
  padding-left: 10px;
  color: $dark-grey-color;
  margin-right: 15px;
  &:before{
    content: " ";
    position: absolute;
    top: 6px;
    left: 0;
    width: 6px;
    height: 6px;
    border-radius: 50%;
  }
}

.tender{
  &:before{
    background-color: $tender-color;
  }
}

.contracts{
  &:before{
    background-color: $contract-color;
  }
}

/* ------ style for table ------- */
.custom-table {
  margin-bottom: 30px;
  width: 100%;
}
